/* main app  */

::-webkit-scrollbar {
  width: 0.6vw;
}
::-webkit-scrollbar-thumb {
  border-radius: 30px;
  background: #9e97e0;
}
.transactionBody {
  font-family: "Lato", sans-serif;
  font-size: 16px;
  margin: 0px;
  background: linear-gradient(to bottom left, #b8b4e0,#6b5fdb );
  filter: drop-shadow(12px 12px 5px rgba(58, 48, 151, 0.3));
  height: 100vh;
  overflow: hidden;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  word-wrap: break-word;
  color:black;
}
h3 {
  margin-top: 1vh;
  margin-bottom: 1vh;
  text-align: center;
}
p {
  color: #5d5b66;
  margin-right: 1vw;
  margin-left: 1vw;
}
.container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background: white;
  border-radius: 3vh;
  height: 75vh;
  width: 85vw;
  padding: 0.5%;
}
.col1 {
  margin-right: 3.5vw;
  height: 75vh;
  width: 45vw;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.col2 {
  height: 75vh;
  width: 26vw;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.line {
  margin-right: 3.5vw;
  border-left: 6px solid #bdb6ff;
  height: 70vh;
  border-radius: 10px;
}
.item {
  display: flex;
  flex-direction: row;
  align-items: center;
}

/* inputs */

input {
  border-top-style: hidden;
  border-right-style: hidden;
  border-left-style: hidden;
  border-bottom-color: #d0cbff;
  background-color: rgba(0, 0, 0, 0);
  color: #4e3fd3;
  outline: none;
  text-align: center;
  margin: none;
  width: 5vw;
  font-size: 20px;
}
input[type="text"] {
  width: 25vw;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* transaction form */

.addTransactionForm {
  text-align: center;
  height: fit-content;
}
.addTransactionInputs {
  justify-content: space-between;
}
.addTransactionForm h3 {
  margin-top: 2vh;
}
.addTransactionForm p {
  font-size: 20px;
  margin-top: 1.5vh;
  margin-bottom: 1.5vh;
}
.addSmall {
  display: none;
}

/* all transactions */

.transactionsItem {
  height: 40vh;
}
.transactionTitle {
  width: 50%;
  text-align: left;
}
.transactionsList {
  height: 35vh;
  overflow: scroll;
  scrollbar-width: thin;
  overflow-x: hidden;
}
.transactionsList p {
  margin: 0px;
  margin-top: 1vh;
  margin-bottom: 1vh;
  margin-right: 1vw;
}
.value {
  width: 25%;
  text-align: right;

}

/* sum transactions */

.sum {
  width: 99%;
  height: 8vh;
}
.sum h3 {
  width: 50%;
  text-align: center;
}
.sum p {
  margin-top: 1vh;
  margin-bottom: 1vh;
}
.sumValue {
  width: 21%;
  text-align: right;
}

/* exchange rate */

.exchangeRateItem {
  height: fit-content;
  min-height: min-content;
  border-radius: 15px;
  border: 3px solid #9e97e0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.exchangeRate {
  font-size: 20px;
  justify-content: center;
  width: 100%;
}
.exchangeRateItem h3 {
  font-size: 24px;
}
.exchangeRate p {
  margin-top: 1vh;
  font-weight: bold;
}
.exchangeRate input {
  margin-bottom: 1vh;
  font-weight: bold;
}

/* biggest transaction */

.biggestTransaction {
  font-size: 20px;
  height: fit-content;
  text-align: center;
  border-radius: 15px;
  border: 3px solid #9e97e0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}
.values {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-bottom: 1vh;
}
.biggestTransaction h3 {
  font-size: 24px;
  margin-top: 1vh;
}
.biggestTransaction p {
  margin: 1vh;
  margin-bottom: 0px;
}
.biggestTransaction p.title {
  font-size: 20px;
  font-weight: bold;
}

/* button */

.button {
  background-color: #4e3fd3;
  border: none;
  border-radius: 20px;
  color: white;
  text-align: center;
  opacity: 0.8;
  transition: 0.3s;
  cursor: pointer;
  padding: 6px;
  width: 12vw;
  margin-top: 1vh;
  margin-bottom: 1vh;
  font-size: 16px;
}
.deleteButton {
  margin: 0px;
  margin-right: 1vw;
  border-radius: 10px;
  padding: 0px;
  width: 1.5em;
  height: 1.5em;
  font-size: 14px;
}
.button:hover {
  opacity: 1;
}

/* media queries */

@media (max-width: 720px), (max-height: 700px) {
  body {
    height: auto;
    overflow: scroll;
  }
  .container {
    flex-direction: column;
    height: auto;
    margin-top: 5vh;
    margin-bottom: 5vh;
  }
  input {
    font-size: 16px;
    width: 60vw;
  }
  input[type="text"] {
    width: 60vw;
    font-size: 16px;
  }
  .col1 {
    width: 80vw;
    margin-right: 0px;
    height: auto;
  }
  .col2 {
    width: 65vw;
    margin-top: 2vh;
    height: fit-content;
  }
  .line {
    display: none;
  }
  .addTransactionForm {
    margin-top: 3vh;
    height: fit-content;
  }
  .addTransactionInputs {
    flex-direction: column;
  }
  .addTransactionForm h3 {
    font-size: 20px;
    margin-top: 0px;
  }
  .addTransactionForm p {
    font-size: 16px;
  }
  .addBig {
    display: none;
  }
  .addSmall {
    display: unset;
  }
  .transactionsItem {
    height: auto;
  }
  .transactionsItem h3 {
    font-size: 20px;
  }
  .transactionsList {
    height: auto;
  }
  .exchangeRateItem {
    height: fit-content;
    margin-bottom: 5vh;
  }
  .exchangeRate {
    margin-bottom: 2vh;
  }
  .exchangeRateItem p {
    margin-block-start: 0.5vh;
    margin-block-end: 0.5vh;
    font-size: 16px;
  }
  .exchangeRate input {
    font-size: 18px;
    font-weight: bold;
    width: 15vw;
    margin-bottom: 0px;
  }
  .exchangeRateItem h3 {
    font-size: 20px;
  }
  .biggestTransaction {
    height: fit-content;
    margin-bottom: 4vh;
  }
  .biggestTransaction h3 {
    font-size: 20px;
  }
  .sum {
    width: 100%;
  }
  .button {
    width: 20vw;
  }
  .deleteButton {
    width: 1.2em;
    height: 1.2em;
  }
}
