.oneBox {
  display: flex;
  justify-content: center;
  margin-left: 4vw;
  margin-right: 4vw;
  margin-bottom: 3vh;
  margin-top: 3vh;
}
.columnBox {
  flex-direction: column;
  width: 70vw;
  margin-left: 15vw;
}
.oneBox > img {
  align-self: center;
  margin: 2vw;
}
.oneBox > div {
  align-self: center;
}
.oneBox > div > h1 {
  font-family: "Montserrat", sans-serif !important;
  text-align: center;
  margin-bottom: 3vh;
  font-weight: bold;
  letter-spacing: 0.5vw;
}
.oneBox > div > h6 {
  font-family: "Montserrat", sans-serif !important;
  margin-left: 2vw;
  margin-top: 3vh;
}
.oneBox > div > p {
  font-family: "Lato", sans-serif !important;
  margin-left: 2vw;
  margin-right: 2vw;
  margin-bottom: 0px;
  text-align: justify;
  color:white;
}
.btn {
  display: flex !important;
  justify-content: center;
  margin: 0px !important;
}
.btn-link {
  font-weight: 1000 !important;
  color: rgb(172, 87, 221) !important;
}
.socialMedia {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  position: absolute;
  right: 0;
  top: 0;
  font-size: 2em;
  margin-right: 1vw;
}
.socialMediaContact {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  font-size: 2.5em;
}

@media (max-width: 375px) {
  .oneBox > div > h1 {
    font-size: 3.5vw !important;
  }
  .oneBox > div > h6 {
    font-size: 3.25vw !important;
  }
  .oneBox > div > p {
    font-size: 3vw !important;
  }
  .oneBox {
    flex-direction: column;
  }
  .reverse {
    flex-direction: column-reverse;
  }
  .oneBox > img {
    max-height: 30vh;
    max-width: 80vw;
  }
  .btn-md {
    padding: 5px 5px !important;
    font-size: 10px !important;
    border-radius: 2px !important;
  }
  .socialMedia {
    font-size: 1.5em;
  }
  .socialMediaContact {
    font-size: 1.75em;
  }
}

@media (min-width: 376px) and (max-width: 759px) {
  .oneBox > div > h1 {
    font-size: 3vw !important;
  }
  .oneBox > div > h6 {
    font-size: 2.75vw !important;
  }
  .oneBox > div > p {
    font-size: 2.5vw !important;
  }
  .oneBox {
    flex-direction: column;
  }
  .reverse {
    flex-direction: column-reverse;
  }
  .oneBox > img {
    max-height: 30vh;
    max-width: 75vw;
  }
  .btn-md {
    padding: 5px 5px !important;
    font-size: 11px !important;
    border-radius: 2px !important;
  }
  .socialMedia {
    font-size: 1.5em;
  }
  .socialMediaContact {
    font-size: 2em;
  }
}

@media (min-width: 760px) {
  .oneBox > div > h1 {
    font-size: 2.5vw !important;
  }
  .oneBox > div > h6 {
    font-size: 1.75vw !important;
  }
  .oneBox > div > p {
    font-size: 1.5vw !important;
  }
  .oneBox > img {
    max-height: 40vh !important;
    max-width: 40vw !important;
  }
}

@media (orientation: landscape) {
  .oneBox > div > h1 {
    font-size: 2vw !important;
  }
  .oneBox > div > h6 {
    font-size: 1.75vw !important;
  }
  .oneBox > div > p {
    font-size: 1.5vw !important;
  }
  .oneBox > img {
    max-height: 50vh;
    max-width: 50vw;
  }
}
