.infoButton{
  position: absolute;
  top:5px;
  left:5px;
  width: 45px;
}
.infoBox {
  font-family: "Press Start 2P", cursive !important;
  font-size: 9px !important;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  text-align: center;
  top:10px;
  left:55px;
  background-color: rgba(175, 0, 0,0.8);
  border-radius: 8px;
}
.infoBox > h6 {
  font-size: 11px !important;
  margin: 0px;
  margin-bottom: 5px;
  text-decoration-line: underline;
}
.info {
  display: flex;
}
.info > div > p {
  margin: 2px;
  text-align: left;
}
.mainContainer {
  font-family: "Press Start 2P", cursive !important;
  font-size: 11px !important;
  align-items: center;
  position: relative;
  height: 100vh;
  width: 100%;
  justify-content: center;
}
.pokedexContainer {
  position: relative;
  height: 100%;
  background-image: url("../../assets/Pokedex/background_80.PNG");
  background-position: 0px 0px;
  background-repeat: repeat round;
  animation: animatedBackground 175s linear infinite;
}
@keyframes animatedBackground {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 0 90%;
  }
}
.pokedexImage {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 700px;
  height: 500px;
  margin-top: -250px;
  margin-left: -350px;
}
.pokemonGrid {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 700px;
  height: 500px;
  margin-top: -250px;
  margin-left: -350px;
  display: grid;
  grid-template-columns: 6% 6% 1% 3% 12% 4% 12% 6% 6% 15.5% 0.5% 4% 11% 6% 5% 2%;
  grid-template-rows: 16% 5% 25% 15% 6% 3% 8% 2% 10% 2% 8%;
  align-items: center;
  justify-content: center;
}
.beigeButtonItem {
  grid-column-start: 10;
  grid-column-end: 11;
  grid-row-start: 6;
  grid-row-end: 8;
}
.resetButtonItem {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 7;
  grid-row-end: 9;
  justify-self: end;
  align-self: end;
}
.spriteItemCrossBg {
  grid-column-start: 2;
  grid-column-end: 8;
  grid-row-start: 3;
  grid-row-end: 5;
  width: 75%;
  background: url("../../assets/Pokedex/black_screen_cross_2.png");
  height: 100%;
  margin-left: 11%;
  margin-top: 1.5%;
  opacity: 0.4;
}
.spriteItem {
  grid-column-start: 2;
  grid-column-end: 8;
  grid-row-start: 3;
  grid-row-end: 5;
  justify-self: center;
}
.crossButtonItem {
  grid-column-start: 7;
  grid-column-end: 8;
  grid-row-start: 7;
  grid-row-end: 10;
}
.pokedexNumberItem {
  grid-column-start: 3;
  grid-column-end: 6;
  grid-row-start: 9;
  grid-row-end: 11;
  text-align: center;
  margin-top: 2%;
  margin-left: 4%;
}
.pokedenNumberItemCrossBg {
  grid-column-start: 3;
  grid-column-end: 6;
  grid-row-start: 9;
  grid-row-end: 11;
  margin-left: 2%;
  margin-bottom: 2%;
  background-image: url("../../assets/Pokedex/green_screen_cross_2.PNG");
  height: 96%;
  width: 102%;
  opacity: 0.1;
}
.pokemonInfoCrossBg {
  grid-column-start: 10;
  grid-column-end: 15;
  grid-row-start: 3;
  grid-row-end: 4;
  width: 100%;
  background: url("../../assets/Pokedex/black_screen_cross_2.png");
  height: 98%;
  opacity: 0.4;
}
.pokemonInfoItem {
  grid-column-start: 12;
  grid-column-end: 15;
  grid-row-start: 3;
  grid-row-end: 4;
  justify-self: center;
  justify-self: left;
  text-align: center;
  width: 100%;
}
.pokemonInfoNamesItem {
  grid-column-start: 10;
  grid-column-end: 12;
  grid-row-start: 3;
  grid-row-end: 4;
  justify-self: center;
  justify-self: left;
  margin-left: 10%;
}
.abilitiesItemCrossBg {
  grid-column-start: 10;
  grid-column-end: 12;
  grid-row-start: 8;
  grid-row-end: 10;
  width: 102%;
  background: url("../../assets/Pokedex/black_screen_cross_2.png");
  height: 96%;
  margin-left: 1%;
  margin-top: 3%;
  opacity: 0.4;
}
.abilitiesItem {
  grid-column-start: 10;
  grid-column-end: 12;
  grid-row-start: 8;
  grid-row-end: 10;
  justify-self: center;
  text-align: center;
  margin-left: 2%;
}
.typesItemCrossBg {
  grid-column-start: 13;
  grid-column-end: 15;
  grid-row-start: 8;
  grid-row-end: 10;
  width: 96%;
  background: url("../../assets/Pokedex/black_screen_cross_2.png");
  height: 96%;
  margin-left: 3%;
  margin-top: 3%;
  opacity: 0.4;
}
.typesItem {
  grid-column-start: 13;
  grid-column-end: 15;
  grid-row-start: 8;
  grid-row-end: 10;
  justify-self: center;
  text-align: center;
}
.beigeBtnItem {
  grid-column-start: 10;
  grid-column-end: 11;
  grid-row-start: 6;
  grid-row-end: 8;
}
.crossButtonGrid {
  margin-top: 5%;
  margin-left: 5%;
  width: 78px;
  height: 78px;
  display: grid;
  grid-template-columns: 19.5px 33px 6.5px 13px 6.5px;
  grid-template-rows: 19.5px 33px 6.5px 13px 6.5px;
}
.crossButtonMiddleItem {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 2;
  grid-row-end: 3;
}
.crossButtonUpItem {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
}
.crossButtonUpShadowItem {
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: 2;
  align-self: stretch;
}
.crossButtonDownItem {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 5;
  z-index: 1;
}
.crossButtonDownShadowItem {
  grid-column-start: 2;
  grid-column-end: 4;
  grid-row-start: 4;
  grid-row-end: 6;
}
.crossButtonLeftItem {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 3;
}
.crossButtonLeftShadowItem {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 3;
  grid-row-end: 4;
  justify-self: end;
}
.crossButtonRightItem {
  grid-column-start: 3;
  grid-column-end: 5;
  grid-row-start: 2;
  grid-row-end: 3;
  z-index: 1;
}
.crossButtonRightShadowItem {
  grid-column-start: 3;
  grid-column-end: 6;
  grid-row-start: 2;
  grid-row-end: 4;
}
.blueLight {
  grid-column-start: 2;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: 2;
}
.yellowLight {
  grid-column-start: 14;
  grid-column-end: 15;
  grid-row-start: 7;
  grid-row-end: 8;
  align-self: start;
  justify-self: end;
}

.tooSmallDevice {
  display: none;
}

@media (max-width: 767px), (max-height: 500px) {
  .mainContainer {
    display: none;
  }
  .tooSmallDevice {
    height: 100vh;
    display: block;
    text-align: center;
    display: flex;
    margin-left: 10vw;
    margin-right: 10vw;
    flex-direction: column;
    justify-content: center;
  }
}
